.card-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-frame {
  background: #f4f1e7;
  width: 100%;
  height: 100%;
  box-shadow: 5px 4px 20px rgba(0, 0, 0, 0.5);
  padding-top: 10%;
  padding-bottom: 19%;
  padding-left: 6%;
  padding-right: 6%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-end;
}

.icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-left: 2%;
  padding-right: 2%;
  color: #605a5a;
  padding-top: 4%;
}

.favoriteCardIcon:hover {
  scale: 1.2;
  cursor: pointer;
}

.rightIcons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 25%;
}

.card-image {
  width: 100%;
  height: 100%;
}

.fade-in {
  -webkit-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
