.searchBar {
  width: 40%;
  min-width: 250px;
  height: 50px;
  background: rgba(217, 217, 217, 0.55);
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0 10px;
}

.searchBarInput {
  width: 80%;
  border: none;
  background: transparent;
  height: 100%;
  font-size: 15px;
  font-family: "Inter";
  font-weight: 600;
  letter-spacing: 0.05em;
}

.searchBarInput:focus {
  outline: none;
}

::placeholder {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 29px;
  letter-spacing: 0.05em;
  color: #7c7c7c;
  padding-right: 2px;
}

.searchIcon {
  color: #7c7c7c;
}

.enterIcon {
  color: #423e3e;
}

.enterIcon:hover {
  scale: 1.1;
  cursor: pointer;
}

.enterIcon:active {
  scale: 1.1;
  color: black;
  cursor: pointer;
}

.searchBar button {
  border: none;
  height: 50%;
  background-color: none;
}
