.cardModalFrame {
  background: #f4f1e7;
  width: 100%;
  height: 100%;
  box-shadow: 5px 4px 20px rgba(0, 0, 0, 0.5);
  padding-top: 8%;
  padding-bottom: 8%;
  padding-left: 6%;
  padding-right: 6%;
  display: flex;
  flex-direction: column;
  gap: 3%;
}

.info {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 1.1em;
  line-height: 16px;
  gap: 10%;
  /* or 118% */

  letter-spacing: 0.46px;

  color: #262323;
}

.descriptionContainer {
  height: 50%;
}

.descriptionField {
  height: 100%;
  background-color: #e5dccf;
  border-radius: 8px;
  padding: 4%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: start;
}

.descriptionValue {
  height: 80%;
  width: 100%;
}

.editIcon {
  color: #605a5a;
}

.editIcon:hover {
  scale: 1.1;
  cursor: pointer;
}

.row {
  display: flex;
  flex-direction: row;
  height: 40%;
  gap: 4%;
}

.column {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 100%;
}

.container {
  height: 50%;
  display: flex;
  flex-direction: column;
  gap: 12%;
}

.field {
  height: 100%;
  background-color: #e5dccf;
  border-radius: 8px;
  padding: 8%;
}

.infoValue {
  font-size: 0.8em;
  color: #605a5a;
}

.closeInfoModal {
  text-align: end;
  color: #d9281c;
}

.closeInfoModal:hover {
  cursor: pointer;
}

.saveContainer {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  height: 10%;
}
#saveButton {
  width: 30%;
  height: 100%;
  border-radius: 20px;
  border: none;
  background-color: #605a5a;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 1em;
  line-height: 26px;

  letter-spacing: 0.46px;

  color: #d9d9d9;
}
#saveButton:hover {
  scale: 1.1;
  cursor: pointer;
}

@media (max-width: 440px) {
  .cardModalFrame {
    width: 80%;
    height: 80%;
  }
  .info {
    font-size: 0.8em;
  }
  #saveButton {
    font-size: 0.8em;
  }
}
