.footer {
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 80px;
  background-color: #262323;
  gap: 5px;
  margin-top: 40px;
}

.footer p {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.05em;

  color: #d9d9d9;
}

.footer a {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.05em;

  color: #d9d9d9;
}

.footer h2 {
  font-family: "Minercraftory";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 31px;
  text-align: center;
  letter-spacing: 0.05em;

  color: #d9d9d9;
}
